<template>
<div>
    <b-form @submit="onSubmit" @reset="onReset" v-if="show">
      <b-form-group id="input-group-1" label="Nom" label-for="input-1">
        <b-form-input
          id="input-1"
          v-model="form.name"
          placeholder="Introduir nom"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Cognoms" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.surname"
          placeholder="Introduir cognoms"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3" label="Adreça electrònica" label-for="input-3" description="La teva informació personal no es compartirà amb ningú.">
        <b-form-input
          id="input-3"
          v-model="form.email"
          type="email"
          placeholder="Introduir email"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-4" label="Contrasenya" label-for="input-4">
        <b-form-input
          id="input-4"
          v-model="form.password"
          placeholder="· · · · · · · · · · "
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-5" label="Confirmar contrasenya" label-for="input-5">
        <b-form-input
          id="input-5"
          v-model="form.confirmpassword"
          placeholder="· · · · · · · · · · "
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-6" v-slot="{ ariaDescribedby }">
        <b-form-checkbox-group
          v-model="form.checked"
          id="checkboxes-6"
          :aria-describedby="ariaDescribedby"
        >
         <b-form-checkbox value="me" required>He llegit i entenc la informació sobre l'ús de les meves dades personals explicada en la Política de Privacitat.</b-form-checkbox>
         <b-form-checkbox value="that">Vull rebre novetats i comunicacions comercials personalitzades a través d'email i altres medis.</b-form-checkbox>
        </b-form-checkbox-group>
      </b-form-group>

      <b-button type="submit" variant="primary">Crear compte</b-button>
    </b-form>
    <!--<b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card>-->
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          surname: '',
          email: '',
          password: '',
          confirmpassword: '',
          checked: [],
        },
        foods: [{ text: 'Select One', value: null }, 'Carrots', 'Beans', 'Tomatoes', 'Corn'],
        show: true
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.name = ''
        this.form.surname = ''
        this.form.email = ''
        this.form.password = ''
        this.form.confirmpassword = ''
        this.form.checked = ''
        
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>

<style scoped>
.form-group{
  padding: 0.5rem 25%;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

.btn-primary {
  background-color: #1FBF57;
  border-color: #1FBF57;
  margin-left: 44%;
  margin-right: 44%;
  margin-top: 2rem;
  margin-bottom: 10rem;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}
</style>